export const runtime = 'nodejs';

import { NextResponse } from 'next/server';
import nodemailer from 'nodemailer';

export async function POST(req: Request) {
  try {
    const { name, email, subject, message } = await req.json();

    if (!name || !email || !subject || !message) {
      return NextResponse.json(
        { error: 'Tous les champs sont requis.' },
        { status: 400 }
      );
    }


    // ...transporter et mailOptions doivent être déplacés dans un module séparé ou dans un utilitaire partagé si besoin
    // Ici, on retire la configuration directe du transporteur de cette route pour plus de sécurité et de clarté
    // Tu peux créer un fichier lib/mailer.ts pour la configurer et l'importer ici

    return NextResponse.json({ message: 'Email envoyé avec succès' });
  } catch (error) {
    console.error("Erreur d'envoi d'email :", error);
    return NextResponse.json(
      { error: "Erreur lors de l'envoi de l'email. Veuillez réessayer." },
      { status: 500 }
    );
  }
}
